/**
 * 
 *
 * @param {number} x x
 * @param {number} y y
 * @param {number} width 
 * @param {number} height 
 * @param {number} px x(x)
 * @param {number} py y(y)
 * @returns {number} 1:up 2:down 3:left 4:right -1:center()
 */
export function getRectDirection(x, y, width, height, px, py) {
    const y_ac =
        ((y + height) * px - y * px + y * (x + width) - (y + height) * x) /
        (x + width - x);
    const y_bd =
        (y * px - (y + height) * px + (y + height) * (x + width) - y * x) /
        (x + width - x);

    if (py < y_ac && py < y_bd) {
        return 1;
    } else if (py > y_ac && py > y_bd) {
        return 2;
    } else if (py > y_ac && py < y_bd) {
        return 3;
    } else if (py < y_ac && py > y_bd) {
        return 4;
    } else {
        //
        const middle_x = x + width / 2;
        const middle_y = y + height / 2;

        if (px > middle_x && py < middle_y) {
            //
            return 1;
        } else if (px > middle_x && py > middle_y) {
            //
            return 2;
        } else if (px < middle_x && py < middle_y) {
            //
            return 1;
        } else if (px < middle_x && py > middle_y) {
            //
            return 2;
        } else {
            //
            return -1;
        }
    }
}

export function isBeforeRect(...a) {
    const ret = getRectDirection(...a);

    return ret === 1 || ret === 3;
}

export function getHoverDirection(...a) {
    const ret = getRectDirection(...a);

    let dir = "down";

    if (ret === 1) {
        dir = "up";
    } else if (ret === 2) {
        dir = "down";
    } else if (ret === 3) {
        dir = "left";
    } else if (ret === 4) {
        dir = "right";
    }

    return dir;
}

export function isNodeInDocument(node) {
    return document.body.contains(node);
}
